@import url("https://fonts.googleapis.com/css?family=Raleway:300,400,700,800");
html {
	font-family: sans-serif;
	/* 1 */
	-ms-text-size-adjust: 100%;
	/* 2 */
	-webkit-text-size-adjust: 100%;
	/* 2 */
}

body {
	font: 400 17px/1.5 "Raleway", sans-serif;
}

/* Layout */
.ba-container {
  padding-left: 20px;
  padding-right: 20px;
  max-width: 1200px;
}

*, *:after, *:before {
  box-sizing: border-box;
}

.cf:before, .cf:after {
  content: " ";
  display: table;
}

.cf:after {
  clear: both;
}

.ba-container, .ba-container-center {
  margin-left: auto;
  margin-right: auto;
}

img {
  max-width: 100%;
  height: auto;
}

html {
  height: 100%;
}

body, html {
  min-height: 100%;
}

/*== Grid ==*/
/* Grid clearfix */
.ba-row:before, .ba-row:after {
  content: " ";
  display: table;
}

.ba-row:after {
  clear: both;
}

.ba-row + .ba-row {
  margin-top: 20px;
}

[class*="ba-width-"] {
  width: 100%;
}

.ba-row > [class*="ba-width-"] {
  float: left;
  padding-left: 20px;
}

.ba-row {
  margin-left: -20px;
}

/* Whole */
.ba-width-1-1 {
  width: 100%;
}

/* Halves */
.ba-width-1-2, .ba-width-2-4, .ba-width-3-6 {
  width: 50%;
}

/* Thirds */
.ba-width-1-3, .ba-width-2-6 {
  width: 33.333%;
}
.ba-width-2-3, .ba-width-4-6 {
  width: 66.666%;
}

/* Quarters */
.ba-width-1-4 {
  width: 25%;
}
.ba-width-3-4 {
  width: 75%;
}

/* Fifths */
.ba-width-1-5 {
  width: 20%;
}
.ba-width-2-5 {
  width: 40%;
}
.ba-width-3-5 {
  width: 60%;
}
.ba-width-4-5 {
  width: 80%;
}

/* Floats */
.ba-float-left {
  float: left;
}

.ba-float-right {
  float: right;
}

.ba-float-none {
  float: none;
}

/* Typography */
a {
  color: currentColor;
  text-decoration: none;
  line-height: 1;
}

a,
input[type="submit"],
input[type="button"],
button{
  text-decoration: none;
	-webkit-transition: color .3s ease-in, border-color .3s ease-in, background-color .3s ease-in, opacity .3s ease-in;
	-moz-transition: color .3s ease-in, border-color .3s ease-in, background-color .3s ease-in, opacity .3s ease-in;
	transition: color .3s ease-in, border-color .3s ease-in, background-color .3s ease-in, opacity .3s ease-in;
}

a:active,
a:hover {
  outline: 0;
}

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  /* 1 */
  font: inherit;
  /* 2 */
  margin: 0;
  /* 3 */
}

button {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  /* 2 */
  cursor: pointer;
  /* 3 */
}

button[disabled],
html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input {
  line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

input[type="search"] {
  -webkit-appearance: textfield;
  /* 1 */
  box-sizing: content-box;
  /* 2 */
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  border: 0;
  /* 1 */
  padding: 0;
  /* 2 */
}

textarea {
  overflow: auto;
}

optgroup {
  font-weight: bold;
}

input[type="submit"],
input[type="button"],
button,
.ba-button {
	display: inline-block;
  border: none;
  outline: none;
}

.ba-button {
  min-height: 55px;
  line-height: 55px;
  font-size: 20px;
  text-transform: uppercase;
  padding: 0 25px;
  color: #FFF;
  background-color: #ff6033;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.5);
  width: 200px;
}

.ba-button:active {
  position: relative;
  top: 1px;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.5);
}

input[type="submit"]:hover,
input[type="button"]:hover,
button:hover,
.ba-button:hover{
	opacity: 0.7;
}

.ba-button:focus {
  outline-color: #fff;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="number"],
input[type="tel"],
textarea,
select {
	-webkit-transition: color .3s ease-in, border-color .3s ease-in, background-color .3s ease-in, opacity .3s ease-in;
	-moz-transition: color .3s ease-in, border-color .3s ease-in, background-color .3s ease-in, opacity .3s ease-in;
	transition: color .3s ease-in, border-color .3s ease-in, background-color .3s ease-in, opacity .3s ease-in;
	display: inline-block;
 	height: 56px;
	line-height: 48px;
	border: 1px solid #848789;
	padding: 0 30px;
	font-style: italic;
	font-weight: 400;
	vertical-align: middle;
	width: 570px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="password"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
textarea:focus,
select:focus {

}
textarea {

