@media (min-width: 840px) {
  .ba-menu li {
    float: left;
    margin-top: 15px;
    padding-left: 39px;
  }
  
  .ba-menu li:first-child {
    padding-left: 0;
  }

  .ba-menu li:last-child {
    margin-right: 20px;
  }
  
  .ba-menu a:before {
    display: block;
    position: absolute;
    content: "";
    background-color: #ff6033;
    height: 1px;
    left: 0;
    right: 0;
    top: -15px;
    opacity: 0;
    transition: all .3s ease-in;
  }

  .ba-menu a:hover:before,
  .ba-menu a.active:before {
    opacity: 1;
    height: 3px;
  }
}

/* Responsive Menu */

.ba-toggle, .ba-toggle:hover, .ba-toggle:focus {
  background: none;
  border: none;
  box-shadow: none;
  width: 30px;
  height: 30px;
  padding: 0;
  line-height: 1;
  min-height: 0;
  margin-top: 20px;
  margin-right: 20px;
  outline: none;
}

.ba-toggle {
  display: none;
}

.ba-toggle__icon {
  z-index: 99;
  width: 30px;
  height: 4px;
  background: #262626;
  display: block;
  position: relative;
  margin: 10px 0;
}

.ba-toggle__icon:hover {
  background: #ff6033;
}

.ba-toggle__icon:hover:before, .ba-toggle__icon:hover:after {
  background: #ff6033;
}

.ba-toggle__icon:before,
.ba-toggle__icon:after {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: #262626;
}

.ba-toggle__icon:before {
  -webkit-transform: translateY(-10px);
      -ms-transform: translateY(-10px);
          transform: translateY(-10px);
}

.ba-toggle__icon:after {
  -webkit-transform: translateY(10px);
      -ms-transform: translateY(10px);
          transform: translateY(10px);
}

.ba-menu-open .ba-toggle__icon {
  background-color: transparent;
}

.ba-menu-open .ba-toggle__icon:before {
  -webkit-transform: translateY(0) rotate(45deg);
      -ms-transform: translateY(0) rotate(45deg);
          transform: translateY(0) rotate(45deg);
}

.ba-menu-open .ba-toggle__icon:after {
  -webkit-transform: translateY(0) rotate(-45deg);
      -ms-transform: translateY(0) rotate(-45deg);
          transform: translateY(0) rotate(-45deg);
}

